html,body,p,ul,li,a,h1,h2,h3,h4,h5,h6,img{margin:0;padding:0;}
html,body{width:100%;height:100%;}
a{text-decoration: none}
ul li{list-style: none}
body{font-size:16px;background:#e1e1e1;color:#333;width:100%;height:100%;}
#app{width:100%;height:100%;}
.xheader{line-height:88px;background:#444;padding:0 20px;}
.flex{display:-webkit-box;display:-ms-flexbox;display:flex;display:-webkit-flex;}
.alignCenter{display:-webkit-box;display:-ms-flexbox;display:flex;display:-webkit-flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;}
.flexCenter{display:-webkit-box;display:-ms-flexbox;display:flex;display:-webkit-flex;-webkit-box-pack: center;-ms-flex-pack: center;justify-content: center}
.flexBetween{display:-webkit-box;display:-ms-flexbox;display:flex;display:-webkit-flex;-webkit-box-pack: justify;-ms-flex-pack: justify;justify-content: space-between}
.flexWrap{display:-webkit-box;display:-ms-flexbox;display:flex;display:-webkit-flex;-ms-flex-wrap:wrap;flex-wrap:wrap;}
.flexColumn{display:-webkit-box;display:-ms-flexbox;display:flex;display:-webkit-flex;-webkit-box-orient: vertical;-webkit-box-direction: normal;-ms-flex-direction: column;flex-direction: column}
.flexEnd{display:-webkit-box;display:-ms-flexbox;display:flex;display:-webkit-flex;-webkit-box-pack: end;-ms-flex-pack: end;justify-content: flex-end}
