.mainTitle{height:40px;line-height:40px;background:#f6f6f6;padding:0 20px;color:#999;}
.registerMain{width:800px; background:#fff;margin-top:8%;border-radius:10px;padding-bottom:50px;}
.w-e-text-container{height:calc(100% - 36px) !important;}
.topic_list{padding:10px;border-top:1px solid #f0f0f0;line-height:30px;}
