.topic_title{color:#333;}
.selectLi{background: #f6f6f6;padding:10px;line-height:30px;}
.selectBtn{color:#80bd01;margin:0 10px;padding:4px 10px;border-radius:5px;}
.activeBtn{color:#fff;background:#80bd01;}
.avator{width:40px;height:40px;margin-right:20px;border-radius:5px;}
.topic_title_short{width:70%;text-overflow:ellipsis;white-space: nowrap;}
.page{margin:0 20px;background:#fff;border-top:1px solid #f0f0f0;padding:20px 0;}
.detailTitle{border-bottom:1px solid #f0f0f0;padding:10px;}
.detail_title{font-size:22px;font-weight:bold;color:#333;margin:10px;line-height:130%;}
.sub_detail_title{margin:10px;font-size:12px;color:#888;}
.detailContent{padding:30px 20px;line-height:28px;}
.commentArea{margin:20px;padding:20px;}
.showArea{margin:20px;background:#fff;padding:20px;border-radius:5px;}
.title{padding:10px 0;color:#333;}
.commentList{margin-bottom:16px;}
.commentTitle{padding:10px 0;}
.commentContent{line-height:30px;}
.colorB{color:#409EFF;}
.isActive{color:#fff;background:#80bd01;}
.activeItem{color:#fff;}
