.el-input__inner:hover{border-color:#c0c4cc;}
.el-input__inner{
	-webkit-appearance:none;
	background-color:#fff;
	background-image:none;
	border-radius:4px;
	border:1px solid #dcdfe6;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	color:#606266;
	display:inline-block;
	height:40px;
	line-height:40px;
	outline:0;
	padding:0 15px;
	-webkit-transition: border-color .2s cubic-bezier(.645,.045,.355,1);
	transition: border-color .2s cubic-bezier(.645,.045,.355,1);
	width:100%;
}
.el-textarea__inner{
	display:block;
	padding:5px 15px;
	line-height:1.5;
	color:#606266;
	background-color:#fff;
	background-image:none;
	border:1px solid #dcdfe6;
	border-radius:4px;
	-webkit-transition:border-color .2s cubic-bezier(.645,.045,.355,1);
	transition:border-color .2s cubic-bezier(.645,.045,.355,1);
}
.el-button{
	display:inline-block;
	line-height:1;
	white-space: nowrap;
	cursor:pointer;
	background:#fff;
	border:1px solid #dcdfe6;
	-webkit-appearance:none;
	text-align:center;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	outline: none;
	-webkit-transition:0.1s;
	transition:0.1s;
	padding:12px 20px;
	font-size:14px;
	border-radius:4px;
}
.el-button--primary{
	color:#fff;
	background:#409EFF;
	border-color:#409EFF;
}
.el-pagination{
	white-space:nowrap;
	padding:2px 5px;
	color:#303133;
	font-weight:700;
}
.el-pager li{
	padding:0 4px;
	background:#fff;
	font-size:13px;
	height:28px;
	line-height:28px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display:inline-block;
}
ul.pagination {
    display: inline-block;
    padding: 0;
    margin: 0;
}
ul.pagination li {
	display:inline-block;
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
     border: 1px solid #ddd;
     cursor:pointer;
}
li.active{
	background-color: #4CAF50;
    color: white;
    border: 1px solid #4CAF50;
}
ul.pagination li:hover:not(.active) {background-color: #ddd;}
.pagination li:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.pagination li:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}